@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    
}
.p{
    font-size: 1.5rem;  
}

 
.img{
    width: 450px;
    height: 600px;   
    border-radius: 30%;  
    padding: 10px 10px; 
}
.about{
    padding-top: 100px;
    padding-right: 60px;
    padding-left: 10px;
}
.aboutHeading{
    font-size: 5rem;
    color: black;
}
.para{
    font-size: 40px;
    color: #000;
} 
@media screen and (max-width:1200px){
    .img{
        width: 300px;
        height: 450px;   
        border-radius: 30%;  
        padding: 10px 10px; 
    }

}
.imgMod {
    width: 100%;
    height: 100%;
    padding: 2%;;
    margin: 3%;
    margin-bottom: 5%;
    margin-top: 5%;
    border-radius: 10%;
}

.projectName {
    color: black !important;
    font-weight: bolder;
}

.imgContainer .imgMod{
    box-shadow: 10px 10px 5px grey;
}
.portfolio {
    color:  black !important;
    text-align: center;
    padding-top: 1%;
    padding-bottom: 5%;
    background-color: #e7e7e1;
    padding-top: 100px;
}

.thumbnailWrapper {
    padding-left: 5%;
    padding-right: 5%;
    text-align: center;
}
.portHead{
    font-size: 5rem;
}
@media screen and(max-width:468px){
    .portHead{
        font-size: 20px;
    }
}


.contact{
    background-color: #221e3f !important;
    height: 100vh;
}
.cardStyle{
    margin-top: 20%;
    background-color: #1d1930;
    padding: 5% 20%;
    -webkit-filter: drop-shadow(1px 1px 5px rgb(138, 140, 141));
            filter: drop-shadow(1px 1px 5px rgb(138, 140, 141));
    opacity: 0.7;
}
.contHead{
    color: seashell;
    padding-bottom: 4%;
    font-size: 2rem;
}
.my-email{
    padding-bottom: 4%;
}
.contact {
    text-align: center;
    padding-top: 100px;
    padding-bottom: 2%;
    color: #eee;
}

.home{
    height: 100vh;
    background-image: url("https://desktopwalls.net/wp-content/uploads/2015/09/Calm%20Mountain%20Lake%204K%20Ultra%20HD%20Desktop%20Wallpaper.jpg");
    background: cover;
    background-repeat: no-repeat;
}
.homeStyle {
    -webkit-filter: drop-shadow(1px 1px 5px rgb(138, 140, 141));
            filter: drop-shadow(1px 1px 5px rgb(138, 140, 141));
    margin:  10% 15%;
    padding-bottom: 10%;
}
.display-1,.display-3{
     color:#000;
}
.icon{
    width: 64px;
    height: 64px;
}
@media screen and (max-width:768px){
    .display-1{
        font-size: 30px;
    }
    .display-3{
        font-size: 20px;
    }
    .icon{
        width: 32px;
        height: 32px;
    }
    .homeStyle{
        margin-top: 35%;
    }
}


.footer {
    background-color: #221e3f;
    font-size: 30px;
    opacity: .70;
    padding: 20px;
    color: white;
    /* bottom: 0;
    left: 0;
    right: 0; */
    text-align: center;
  }
  .icon{
      color: red;
  }

